/* src/components/Login/Login.css */

.login-container {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f5f6fa;
}

.login-form {
  background: white;
  padding: 2rem;
  border-radius: 8px;
  width: 320px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
}

.login-form h2 {
  margin-bottom: 1.5rem;
  text-align: center;
  font-weight: 500;
}

.login-form input {
  padding: 10px;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 1rem;
}

.login-form button {
  padding: 10px;
  background-color: #2f80ed;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.2s ease;
}

.login-form button:hover {
  background-color: #1366d6;
}

.error {
  color: red;
  margin-bottom: 1rem;
  text-align: center;
}

.logout-message {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  padding: 2rem;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.logout-message h2 {
  margin: 0;
  font-size: 1.5rem;
  color: #333;
}

.logout-message button {
  position: relative;
  padding: 0.5rem 1.5rem;
  font-size: 1rem;
  background-color: #c0392b;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.logout-message button:hover {
  background-color: #e74c3c;
}
